<template>
  <div class="login" :style="{ height: height + 50 + 'px' }">
    <div>
      <el-card class="login-form-layout">
        <el-form
          autoComplete="on"
          :model="loginForm"
          :rules="rules"
          ref="loginForm"
          label-position="left"
        >
          <div style="text-align: center"></div>
          <h2 class="login-title color-main">后台管理</h2>
          <el-form-item prop="name">
            <el-input
              name="name"
              v-model="loginForm.name"
              placeholder="请输入用户名"
            >
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              name="password"
              v-model="loginForm.password"
              placeholder="请输入密码"
            >
            </el-input>
          </el-form-item>
          <el-form-item style="margin-bottom: 60px; text-align: center">
            <el-button style="width: 45%" type="primary" @click="login">
              登录
            </el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
    <p style="width: 100%; font-weight: bold">鄂ICP备2022019382号-1</p>
  </div>
</template>

<script>
export default {
  name: "login-com",
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loginForm: {
        name: "",
        password: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 1,
            max: 24,
            message: "长度在 1 到 24 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 1,
            max: 24,
            message: "长度在 1 到 24 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {},
  methods: {
    login() {
      this.$refs["loginForm"].validate((valid) => {
        if (valid) {
          this.$reqPost(`/authority/login`, {
            ...this.loginForm,
          }).then((res) => {
            if (res.code == 200) {
              if (!res.data) {
                this.$message.error("用户名或密码错误");
              } else {
                localStorage.setItem("role", JSON.stringify(res.data));
                this.$router.push({ name: "user" });
              }
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
};
</script>

<style scoped>
#app .login {
  background: #00a191;
  display: flex;
  justify-content: center;
  align-items: end;
  flex-wrap: wrap;
}
.login-form-layout {
  width: 360px;
}
.login-title {
  text-align: center;
}
</style>
